import Swiper, { Navigation, Pagination, Autoplay, FreeMode, Mousewheel, EffectFade, Thumbs} from 'swiper';

const jsSliderImportantly = new Swiper('.jsSliderImportantly .slider__inner', {
	modules: [Navigation, Pagination, Autoplay],
	slidesPerView: 1,
    spaceBetween: 20,
    speed: 900,
    autoHeight: true,

	pagination: {
		el: ".jsSliderImportantly .swiper-pagination",
		clickable: true,
		bulletClass: "slider__pagination-item",
	},
    navigation: {
        nextEl: '.jsSliderImportantly .swiper-button-next',
        prevEl: '.jsSliderImportantly .swiper-button-prev',
    },

	breakpoints: {
		0:{
            spaceBetween: 12,
		},

		834: {
            spaceBetween: 16,
		},

        992: {
			pagination: false,
            spaceBetween: 20,
		},

		1260: {
            pagination: false,

		},
	}
});


document.addEventListener('DOMContentLoaded', () => {
    if(document.querySelector('.jsSliderArticle')){
        let items = document.querySelectorAll('.jsSliderArticle');
        for (let index = 0; index < items.length; index++) {
            const item = items[index];
            
            const jsSliderArticle = new Swiper( item.querySelector('.slider__inner'), {
                modules: [Navigation, Pagination],
                loop: false,
                slidesPerView: 1,
                spaceBetween: 16,
                speed: 500,
                
                navigation: {
                    nextEl: item.querySelector('.swiper-button-next'),
                    prevEl: item.querySelector('.swiper-button-prev'),
                },
            
                pagination: {
                    el: item.querySelector(".swiper-pagination"),
                    clickable: true,
                    bulletClass: "slider__pagination-item",
                },
            });
        }
    } 

    if(document.querySelector('.jsSliderDatepciker')){
        let items = document.querySelectorAll('.jsSliderDatepciker');

        for (let index = 0; index < items.length; index++) {
            const item = items[index];
            
            const jsSliderArticle = new Swiper( item.querySelector('.slider__inner'), {
                modules: [Navigation,Mousewheel, FreeMode],
                loop: false,
                slidesPerView: 'auto',
                spaceBetween: 24,
                speed: 500,
                freeMode: true,
                mousewheel: true,
                initialSlide: 1,
                
                navigation: {
                    nextEl: item.querySelector('.swiper-button-next'),
                    prevEl: item.querySelector('.swiper-button-prev'),
                },
            });
        }
    }
})




