document.addEventListener('DOMContentLoaded', function(){

    window.maps = {
        list: document.querySelectorAll('.jsMap'), 
    }
    
    for (let index = 0; index < window.maps.list.length; index++) {
        const item = window.maps.list[index];
        let coord = item.getAttribute('data-map-coordinates') ? JSON.parse(item.getAttribute('data-map-coordinates')) :  [[59.938955, 30.315644]];
        let zoom = item.getAttribute('data-map-zoom') ? item.getAttribute('data-map-zoom') : 16;

        function init(){

            window.maps['item_' +index] = {
                element: item,
                zoom: zoom,
                coordinates: item.getAttribute('data-map-coordinates') ? JSON.parse(item.getAttribute('data-map-coordinates')) : '',

                map: new ymaps.Map(item, {
                    controls: [],
                    center: coord[0],
                    zoom: zoom
                }),

                initDots: function(){
                    if(this.coordinates != ''){
                        for (let dotIndex = 0; dotIndex < this.coordinates.length; dotIndex++) {
                            const thisDot = this.coordinates[dotIndex];
                            this.addMark(thisDot);
                        }
                    }
                },

                addMark: function (cors){

                    placemark = new ymaps.Placemark(cors, {
                        
                    }, {
                        iconLayout: 'default#image',
                        iconImageHref: 'assets/media/geo-dot.svg',
                        iconImageSize: [32, 32],
                        iconImageOffset: [-16, -16]
                    }),

                    this.map.geoObjects.add(placemark) 

                    this.centeredMap();
                },

                centeredMap: function () {
                    this.map.setBounds(this.map.geoObjects.getBounds(),{
                        checkZoomRange: true, 
                        zoomMargin: 40
                    });
                },
            }

            window.maps['item_' +index].initDots();
        }

        ymaps.ready(init)
    }

})