import * as yup from 'yup';

const MSG_LETTERS = '    ';
const MSG_REQUIRED = '   ';
const MSG_TEXT = '    ';
const MSG_TEXT_MIN = '     ';
const MSG_TEXT_MAX = '     ';
const MSG_PHONE = '   ';
const MSG_EMAIL = 'Email   ';
const MSG_NUMBER = '   ';
const MSG_NUMBER_MIN = '    ';
const MSG_NUMBER_MAX = '    ';

const validateLetters = (value, min, max) => {
  const lettersRegExp = /^[A-Z-a-z-\s]*$/;

  const scheme = yup
    .string()
    .trim()
    .required(MSG_REQUIRED)
    .matches(lettersRegExp, MSG_LETTERS)
    .min(min, MSG_TEXT_MIN)
    .max(max, MSG_TEXT_MAX);

  return scheme.isValidSync(value) ? 'valid' : 'invalid';
};

const validateText = (value, min, max) => {
  const scheme = yup
    .string(MSG_TEXT)
    .trim()
    .required(MSG_REQUIRED)
    .min(min, MSG_TEXT_MIN)
    .max(max, MSG_TEXT_MAX);

  return scheme.isValidSync(value) ? 'valid' : 'invalid';
};

const validatePhone = (value) => {
  const phoneRegExp = /^((8|\+7)[- ]?)?(\(?\d{3}\)?[- ]?)?[\d\- ]{7,10}$/;

  const scheme = yup
    .string()
    .trim()
    .required(MSG_REQUIRED)
    .matches(phoneRegExp, MSG_PHONE);
  return scheme.isValidSync(value) ? 'valid' : 'invalid';
};

const validateEmail = (value) => {
  const scheme = yup
    .string(MSG_TEXT)
    .trim()
    .required(MSG_REQUIRED)
    .email(MSG_EMAIL);

  return scheme.isValidSync(value) ? 'valid' : 'invalid';
};

const validateNumber = (value, min, max) => {
  const scheme = yup
    .number(MSG_NUMBER)
    .required(MSG_REQUIRED)
    .min(min, MSG_NUMBER_MIN)
    .max(max, MSG_NUMBER_MAX);

  return scheme.isValidSync(value) ? 'valid' : 'invalid';
};

const validateCheckbox = (value) => {
  const scheme = yup
    .boolean()
    .oneOf([true]);

  return scheme.isValidSync(value) ? 'valid' : 'invalid';
};

const validatorServices = {
  validateLetters,
  validateText,
  validatePhone,
  validateEmail,
  validateNumber,
  validateCheckbox,
};

export default validatorServices;
